@import '../../sass/mixin';

.sec3Img {
    height: 22rem;
    width: 30rem;
}
.sec1text {
    margin: 7rem;
    text-align: left;
}
.sec3text{
    margin: 5rem;
    text-align: left;
}
.sec4text{
    margin:5rem;
    text-align: left;
    color:#fff;
}
.headertxt{
    font-size: 40px;
    margin: 0%;
}
.homeContainer {
    background-color: #fff;
}
.sec4div{
    background-color:#1B252C ;
    width:35vw;
}
.shopdevice{
    @include button;
    background-color:#1B252C ;
    border: 1px solid #fff;
    color:#fff;
}

.ImgBottom {
    width: 300px;
    height: 50px;
    background: #1B252CD9 0% 0% no-repeat padding-box;
    color: #fff;
    z-index: -1;
    border-bottom: 0.5rem solid $bg-nav;
    padding:1rem;
}

.shopbtn {
    @include button;
}
.shopcollbtn{
    @include button;
    background-color: #fff;
    color:#E26A2C;
    border: 1px solid #E26A2C;
}